<template lang="">
	<div :class="['color-item', {'color-item--locked':locked}]">
		<h2 @click="copyToClickboard($event)"></h2>
		<button @click="locked = !locked">
			<font-awesome-icon v-if="locked" icon="fa-solid fa-lock" />
			<font-awesome-icon v-else icon="fa-solid fa-lock-open" />
		</button>
	</div>
</template>
<script>
import { ref } from 'vue'
export default {
	name:'color-item',
	setup(){
		const locked = ref(false)

		const copyToClickboard = (e) => {
			
			return navigator.clipboard.writeText(e.srcElement.textContent)
		}
		return {
			locked,
			copyToClickboard
		}
	}
	
}
</script>

<style scoped lang="scss" src="./style.scss"/>
